import axios from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import { useParams } from 'react-router'

import colorSyntax from '@toast-ui/editor-plugin-color-syntax'
import '@toast-ui/editor-plugin-color-syntax/dist/toastui-editor-plugin-color-syntax.css'
import '@toast-ui/editor/dist/i18n/ko-kr'
import '@toast-ui/editor/dist/toastui-editor.css'
import { type HookCallback } from '@toast-ui/editor/types/editor'
import { Editor } from '@toast-ui/react-editor'

import 'tui-color-picker/dist/tui-color-picker.css'

interface TextEditorProps {
  initialValue: string
  editorRef: React.LegacyRef<Editor>
  toolbarItems: string[][]
}

const TextEditor: React.FC<TextEditorProps> = ({ initialValue, editorRef, toolbarItems }) => {
  const params = useParams()
  const { enqueueSnackbar } = useSnackbar()
  const authHeader = useAuthHeader()

  const uploadImage = async (blob: Blob | File) => {
    try {
      const formData = new FormData()
      formData.append('img', blob)

      const result = await axios.post(`${process.env.REACT_APP_APIHOST}/club/${params.id}/recruit/img`, formData, {
        headers: { Authorization: authHeader() }
      })

      return result.data.url
    } catch (error) {
      enqueueSnackbar('사진 첨부에 실패했습니다. 사진 용량을 줄여 주십시오.', { variant: 'error' })
    }
  }

  const onUploadImage = async (blob: Blob | File, callback: HookCallback) => {
    const url = await uploadImage(blob)
    callback(url, '첨부 이미지')

    document.querySelectorAll('img').forEach(img => {
      img.style.maxWidth = '100%'
    })

    return false
  }

  return (
    <div className="edit_wrap">
      <Editor
        previewStyle={window.innerWidth > 1000 ? 'vertical' : 'tab'}
        height={window.innerWidth > 1000 ? '80vh' : '60vh'}
        initialEditType="wysiwyg"
        ref={editorRef}
        language="ko-KR"
        initialValue={initialValue}
        hooks={{
          addImageBlobHook: onUploadImage
        }}
        toolbarItems={toolbarItems}
        plugins={[colorSyntax]}
      />
    </div>
  )
}

export default TextEditor
