import type { AxiosResponse } from 'axios'
import axios, { AxiosError } from 'axios'
import type { Dayjs } from 'dayjs'
import dayjs from 'dayjs'
import { useSnackbar } from 'notistack'
import { useEffect, useRef, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'
import { useRecoilValue } from 'recoil'

import MobileDatePicker from '@mui/lab/MobileDatePicker'
import { Box, Card, Container, Grid, TextField } from '@mui/material'
import type { Editor } from '@toast-ui/react-editor'

import { COMMON_MESSAGE } from 'src/data/messages'
import { FIRST_SEMESTER, FIRST_SEMESTER_NAME, SECOND_SEMESTER_NAME } from 'src/data/semester'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import DateForm from 'src/components/DateForm'
import ErrorMessageBox from 'src/components/ErrorMessageBox'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import TextEditor from 'src/components/TextEditor'

import PageHeader from 'src/content/manage/PageHeader'

import useSafeAuthUser from 'src/hooks/useSafeAuthUser'
import { dateState } from 'src/recoil/atom'

interface RecruitAPIResponse {
  contents: string
  startAt: string
  semester?: string
  endAt?: string
  limit?: number
  instagramId?: string
}

const Intro: React.FC = () => {
  const date = useRecoilValue(dateState)
  const { id } = useParams()
  const clubId = Number(id)
  const authHeader = useAuthHeader()
  const { userInfo } = useSafeAuthUser()
  const club = userInfo?.clubs.find(club => club.id === clubId)

  const [startAt, setStartAt] = useState<Dayjs>(dayjs('2023-01-01T00:00:00'))
  const [endAt, setEndAt] = useState<Dayjs>(dayjs('2023-01-01T00:00:00'))
  const [limit, setLimit] = useState<number>(0)

  const [isExistRecruit, setIsExistRecruit] = useState(true)
  const [instagramId, setInstagramId] = useState<string | null>('')
  const [isEditMode, setIsEditMode] = useState<boolean>(false)
  const [isRefresh, setIsRefresh] = useState(false)

  const { enqueueSnackbar } = useSnackbar()
  const editorRef = useRef<Editor>(null)

  const handleSubmit = () => {
    if (endAt.isBefore(startAt)) {
      enqueueSnackbar('종료일이 시작일보다 빠를 수 없습니다.', { variant: 'error' })
      return
    }

    // 모집 종료일 기준으로 학기 나눔
    axios({
      method: isEditMode ? 'put' : 'post',
      url: `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit?semester=${date.semester}&year=${date.year}`,
      data: {
        contents: editorRef.current?.getInstance().getHTML(),
        startAt: startAt,
        endAt: endAt,
        limit: limit || 0,
        instagramId: instagramId
      },
      headers: { Authorization: authHeader() }
    })
      .then(res => {
        if (res.data.message === 'ok') {
          enqueueSnackbar('등록이 완료되었습니다.', { variant: 'success' })
          setIsRefresh(prev => !prev)
        } else {
          throw new Error()
        }
      })
      .catch(() => {
        enqueueSnackbar('등록에 실패했습니다. 다시 시도하여 주십시오.', { variant: 'error' })
      })
  }

  useEffect(() => {
    // TODO : url 뒤에 / 떼면 301 Moved Permanently 에러뜸
    axios
      .get(`${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/`, {
        params: date
      })
      .then((res: AxiosResponse<RecruitAPIResponse>) => {
        setStartAt(dayjs(res.data.startAt).startOf('date'))
        setEndAt(dayjs(res.data.endAt).startOf('date'))
        setLimit(res.data.limit || 0)
        setInstagramId(res.data.instagramId || null)
        setIsEditMode(true)
        setIsExistRecruit(true)

        editorRef.current?.getInstance().setHTML(res.data.contents)
      })
      .catch(err => {
        if (err instanceof AxiosError && err.code === 'ERR_BAD_REQUEST') {
          setIsEditMode(false)
          setIsExistRecruit(false)
          editorRef.current?.getInstance().setHTML('')
          return
        }
        enqueueSnackbar(COMMON_MESSAGE.SERVER_ERROR, { variant: 'error' })
      })
  }, [date, isRefresh])

  return (
    <>
      <Helmet>
        <title>소개 관리</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club?.name ?? ''} link={`/manage/${clubId}/attendance/register`} />
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <DateForm club={club} />
        {!isExistRecruit && (
          <ErrorMessageBox
            children={`${date.year}년도 ${
              date.semester === FIRST_SEMESTER ? FIRST_SEMESTER_NAME : SECOND_SEMESTER_NAME
            }에는 모집공고가
          존재하지 않습니다. 새로 작성하시거나 다른 학기를 선택해 주세요.`}
          />
        )}
        <Card>
          <TextEditor
            initialValue=""
            editorRef={editorRef}
            toolbarItems={[
              ['heading', 'bold', 'italic', 'strike'],
              ['hr', 'quote'],
              ['ul', 'ol', 'task'],
              ['table', 'image', 'link'],
              ['code', 'codeblock']
            ]}
          />
        </Card>
        <Box mt={2} mb={2}>
          <Card sx={{ padding: { xs: '20px', md: '30px' } }}>
            <Grid container justifyContent="center" spacing={2}>
              <Grid item sm={12} md={3}>
                <MobileDatePicker
                  label="모집 시작일"
                  inputFormat="yyyy/MM/dd"
                  value={startAt}
                  onChange={newValue => {
                    setStartAt(dayjs(newValue))
                  }}
                  renderInput={params => <TextField {...params} />}
                />
              </Grid>
              <Grid item sm={12} md={3}>
                <MobileDatePicker
                  label="모집 종료일"
                  inputFormat="yyyy/MM/dd"
                  value={endAt}
                  onChange={newValue => {
                    setEndAt(dayjs(newValue))
                  }}
                  renderInput={params => <TextField {...params} />}
                />
              </Grid>
              <Grid item sm={12} md={3}>
                <TextField
                  label="인원 제한(없으면 0, 최대 999명)"
                  id="limit"
                  value={limit}
                  type="number"
                  onChange={e => {
                    setLimit(parseInt(e.target.value) > 999 ? 999 : parseInt(e.target.value))
                  }}
                ></TextField>
              </Grid>
              <Grid item sm={12} md={3}>
                <TextField
                  label="동아리 인스타그램 ID"
                  id="instagramId"
                  value={instagramId}
                  onChange={e => {
                    setInstagramId(e.target.value)
                  }}
                ></TextField>
              </Grid>
            </Grid>
          </Card>
          <CancelSubmitBtns handleSubmit={handleSubmit} submitBtnText="등록 / 수정" />
        </Box>
      </Container>
    </>
  )
}

export default Intro
