import { Link, useParams } from 'react-router-dom'

import { Box, Grid, Typography } from '@mui/material'
import { styled } from '@mui/material/styles'

import { PureLightTheme } from 'src/theme/schemes/PureLightTheme'

const TabItem = styled(Grid)(`
    padding: 10px;
    text-align: center;
`)

function TabMenu() {
  const params = useParams()
  const clubId = params.id

  const menus = [
    { name: '회원 명단', link: `/manage/${clubId}/member` },
    { name: '출석 관리', link: `/manage/${clubId}/attendance` },
    { name: '회비 관리', link: `/manage/${clubId}/fee` },
    { name: '소개 관리', link: `/manage/${clubId}/intro` },
    { name: '신청폼 관리', link: `/manage/${clubId}/form` },
    { name: '동아리 설정', link: `/manage/${clubId}/settings` }
  ]

  return (
    <Box mt={3}>
      <Grid container justifyContent="space-between">
        {menus.map((menu, index) => {
          return (
            <TabItem
              item
              key={index}
              xs={6}
              sm={4}
              md={2}
              sx={
                window.location.pathname.startsWith(menu.link)
                  ? { backgroundColor: PureLightTheme.colors.secondary.main, borderRadius: '10px' }
                  : {}
              }
            >
              <Link to={menu.link} style={{ textDecoration: 'none', color: 'black' }}>
                <Typography variant="h4" sx={window.location.pathname.startsWith(menu.link) ? { color: 'white' } : {}}>
                  {menu.name}
                </Typography>
              </Link>
            </TabItem>
          )
        })}
      </Grid>
    </Box>
  )
}

export default TabMenu
