import { useEffect } from 'react'
import { useRecoilState } from 'recoil'

import {
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  type SelectChangeEvent,
  Stack,
  useMediaQuery,
  useTheme
} from '@mui/material'

import { DATE_MESSAGE } from 'src/data/messages'
import { FIRST_SEMESTER, SECOND_SEMESTER } from 'src/data/semester'

import { type ClubMemberForAuth } from 'src/models/ClubMember'
import { type DateProps, type Semester, defaultDate, isSemester, isYear } from 'src/models/date'

import { clubDateState, dateState } from 'src/recoil/atom'
import { useSnackbar } from 'notistack'

interface DateFormProps {
  club: ClubMemberForAuth | undefined
}

const DateForm: React.FC<DateFormProps> = ({ club }) => {
  const [date, setDate] = useRecoilState(dateState)
  const [clubDate, setClubDate] = useRecoilState(clubDateState)

  useEffect(() => {
    const newDate: DateProps = {
      year: club?.year || defaultDate.year,
      semester: club?.semester || defaultDate.semester
    }

    if (newDate.year !== clubDate.year || newDate.semester !== clubDate.semester) {
      setDate(newDate)
      setClubDate(newDate)
    }
  }, [])

  const { enqueueSnackbar } = useSnackbar()
  const theme = useTheme()
  const isUpperThanSm = useMediaQuery(theme.breakpoints.up('sm'))
  const { year, semester } = date

  const years = Array.from({ length: 6 }).map((_, index) => clubDate.year - index) // 5년 전부터 현재 년도까지의 년도 리스트

  // (2023, 1) 일때, (2022, 2) --> (2023, 2) 로 넘어가는 것 방지
  const isNonforwardableYear = (year: number) => {
    return year === clubDate.year && clubDate.semester === FIRST_SEMESTER && semester === SECOND_SEMESTER
  }

  const handleDateChange = ({ target: { value } }: React.ChangeEvent<HTMLInputElement> | SelectChangeEvent<string>) => {
    if (isSemester(value)) {
      setDate(prevDate => ({ ...prevDate, semester: value as Semester }))
      return
    }
    if (isYear(value)) {
      setDate(prevDate => ({ ...prevDate, year: Number(value) }))
      return
    }
    enqueueSnackbar(DATE_MESSAGE.INVALID_DATE, { variant: 'warning' })
  }

  return (
    <Stack
      width="100%"
      direction={'row'}
      spacing={1}
      justifyContent={isUpperThanSm ? 'flex-end' : 'center'}
      sx={{ marginBottom: 3 }}
    >
      <FormControl size="small">
        <InputLabel id="year-select-label">년도</InputLabel>
        <Select
          labelId="year-select-label"
          id="year-select"
          name="year"
          value={year as unknown as string}
          label="년도"
          onChange={handleDateChange}
          sx={{ width: isUpperThanSm ? '180px' : '40vw' }}
        >
          {years.map(listedYear => (
            <MenuItem key={listedYear} value={listedYear} disabled={isNonforwardableYear(listedYear)}>
              {listedYear}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <FormControl size="small">
        <InputLabel id="semester-select-label">학기</InputLabel>
        <Select
          labelId="semester-select-label"
          id="semester-select"
          name="semester"
          value={semester}
          label="학기"
          onChange={handleDateChange}
          sx={{ width: isUpperThanSm ? '180px' : '40vw' }}
        >
          <MenuItem value={FIRST_SEMESTER}>1학기</MenuItem>

          <MenuItem value={SECOND_SEMESTER} disabled={clubDate.year === year && clubDate.semester === FIRST_SEMESTER}>
            2학기
          </MenuItem>
        </Select>
      </FormControl>
    </Stack>
  )
}

export default DateForm
