import { FIRST_SEMESTER, FIRST_SEMESTER_MONTH, SECOND_SEMESTER, SECOND_SEMESTER_MONTH } from 'src/data/semester'

export type Semester = 'SPRING' | 'FALL'

export type DateProps = {
  semester: Semester
  year: number
}

const today = new Date()

export const isSemester = (text: Semester | string): text is Semester => {
  return text === 'SPRING' || text === 'FALL'
}

export const isYear = (text: Semester | number | string): text is number => {
  return typeof text === 'number'
}

export const getCurrentSemester = (): Semester => {
  const currentMonth = today.getMonth()
  const isFirstSemester = FIRST_SEMESTER_MONTH <= currentMonth && currentMonth < SECOND_SEMESTER_MONTH
  return isFirstSemester ? FIRST_SEMESTER : SECOND_SEMESTER
}

export const defaultDate: DateProps = {
  year: today.getFullYear(),
  semester: getCurrentSemester()
}
