import { useNavigate } from 'react-router-dom'

import { Button, Stack } from '@mui/material'

interface CancelSubmitBtnsProps {
  handleSubmit: React.MouseEventHandler<HTMLButtonElement>
  handleCancel?: React.MouseEventHandler<HTMLButtonElement>
  displayCancelBtn?: boolean
  submitBtnText?: string
  cancelBtnText?: string
}

const CancelSubmitBtns: React.FC<CancelSubmitBtnsProps> = ({
  handleSubmit,
  handleCancel,
  displayCancelBtn = true,
  submitBtnText = '등록',
  cancelBtnText = '취소'
}: CancelSubmitBtnsProps) => {
  const navigate = useNavigate()

  const _handleCancel: React.MouseEventHandler<HTMLButtonElement> = event => {
    if (handleCancel === undefined) navigate(-1)
    else handleCancel(event)
  }

  return (
    <Stack mt={2} pb={2} direction="row" spacing={2} justifyContent="center">
      {displayCancelBtn && (
        <Button color="error" variant="outlined" sx={{ width: '50%', maxWidth: '120px' }} onClick={_handleCancel}>
          {cancelBtnText}
        </Button>
      )}

      <Button color="success" variant="contained" onClick={handleSubmit} sx={{ width: '50%', maxWidth: '120px' }}>
        {submitBtnText}
      </Button>
    </Stack>
  )
}

export default CancelSubmitBtns
